import React from 'react';
import GMap from '../../components/GMap/map.component';
import Popup from "reactjs-popup";

import "./homepage.styles.scss";

const Homepage = () => (
    <div className='profile' id="Homepage">
        <h1 className='title'>Profile</h1>
        <center>
            <p className='subtitle'> I'm a full stack developer, that likes to get the job done</p>
            </center>
        <hr></hr>
        <div className="row">
            <div className="column">
                <h2 className="title2">Summary</h2>
                    <p className="writing"><b> Hello all visitors!</b> <br /></p>
                    <p className="writing">
                    This is my personal site for the advancement and in-depth look into my life and my pursuit of a
                    career in Computer Science. I wish for all that see this site that I made, to understand my
                    values, my skills, and my aspirations. I will answer honest questions and respond to all serious
                    inquiries via my email seen on my contact page. Please enjoy this brief look into my persona.
                    Thank you.</p>
            </div>
            <div className="column">
                <img className='profile-picture' src={require('../../assets/Capture.PNG')} alt='website logo'></img>
            </div>
            <div className="column">
                <h2 className="title2">Details</h2>
                <b className="writing2">Name:</b>
                <p className='writing'>Robert Ionut Dumitrescu</p>
                <b className="writing2">Age:</b>
                <p className='writing'>26 years</p>
                <b className="writing2">Location:</b>
                <Popup modal trigger={<div className="on"><span className="glyphicon glyphicon-map-marker" aria-hidden="true">Lindenwold, New Jersey, United Sates</span></div>}>
                    <div className="size">
                        <GMap></GMap>
                    </div>
                </Popup>
            </div>
        </div>
    </div>
)

export default Homepage;